import React, {Component} from 'react'
import AddCoursesDisplay from './AddCoursesDisplay';
import axios from 'axios'
import toastr from 'toastr'

class AddCoursesContainer extends Component{
  
state = {
  teachers :[],
  classrooms : [],
  course:{}
}

  onChange = (e) => {
    console.log(e.target)
    const field = e.target.name
    const value = e.target.value
    const {course} = this.state
    course[field] = value
    console.log(course)
    this.setState({course})
  }
  
  componentWillMount() {

    axios.get('http://localhost:3000/admin/teachersSelect',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res => {
        this.setState({
          teachers: res.data})
      }).catch(e=>{
        console.log(e)
      })

      axios.get('http://localhost:3000/admin/classroomsSelect',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res => {
        this.setState({
          classrooms: res.data})
      }).catch(e=>{
        console.log(e)
      })
  }

  createCourse = (e) => {
    e.preventDefault()
    const {course} = this.state
    axios.post('http://localhost:3000/admin/addCourses', course,{
      headers:{
        'Authorization':localStorage.getItem('token')            
      }
    })
    .then(user=>{
        console.log(user)
        toastr.success("Exito! Agregaste un curso")
    })
    .catch(e=>toastr.error("Error"))
}


  render(){
    const {teachers, classrooms, course} = this.state
    console.log(this.state)
    return(
      <AddCoursesDisplay
      createCourse={this.createCourse}
      teachers = {teachers}
      classrooms = {classrooms}
      course={course}
      onChange={this.onChange}
      />
    )
  }
}

export default AddCoursesContainer