import React, { Component } from 'react';
import StudentsListDisplay from './StudentsListDisplay';

import axios from 'axios';

class StudentsListContainer extends Component {

  state = {
    data: [],
    columns: [
      { title: 'Username', dataIndex: 'username', key: 'name' },
      { title: 'Email', dataIndex: 'email', key: 'email' },
      { title: 'Classroom', dataIndex: 'classroom', key: 'classroom' },
      { title: 'Empeño', dataIndex: 'empeño', key: 'empeño' },
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.editStudent(obj._id)}>Edit</button>)},
      { title: '', key: 'action', render: (i, obj) => (<button className='button' onClick={()=>this.removeStudent(obj._id)}>Delete</button>)}
    ]
  }

  componentDidMount() {

    axios.get('http://localhost:3000/admin/manageStudents',{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    })
      .then(res => {
        this.setState({
          data: res.data})
      })
  }

  removeStudent=(_id)=>{    
    axios.post('http://localhost:3000/admin/deleteStudent',{_id},{
      headers:{
        'Authorization':localStorage.getItem('token')
      }
    }).then(res =>{
      console.log('se borro!')
      let {data} = this.state
      data = data.filter(d=>d._id!==_id)
      this.setState({data})
    })
    .catch(e=>{
      console.log(e)
    })
  }

  render() {
    const {columns, data} = this.state
    console.log(this.state.data)
    return (
      <div>
        <StudentsListDisplay columns={columns} data={data} />
      </div>
    );
  }
}

export default StudentsListContainer;